@import url(https://fonts.googleapis.com/css?family=Nunito|Open+Sans&display=swap);
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

/*/-_-_-_-_-_-_-_-_    Header   -_-_-_-_-_-_-_-_ /*/

.menu_nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 82px;
}

/*/-_-_-_-_-_-_-_-_-_   Logo  _-_-_-_-_-_-_-_-_ /*/

.menu_img {
  object-fit: contain;
  width: 140px;
}

/*/-_-_-_-_-_-_   Menu Hamburguer  -_-_-_-_-_-_ /*/

.menu_hamburger {
  border: 0;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  right: 20px;
}

@media (min-width: 980px) {
  .menu_hamburger {
    display: none;
  }
}

@media (max-width: 980px) {
  .menu_hamburger {
    display: flex;
  }
}

/*/-_-_-_-_-_-_-_-_-_   Menu  _-_-_-_-_-_-_-_-_ /*/

@media (min-width: 980px) {
  .menu {
    width: 100%;
    height: 100%;
    background: none;
    flex-direction: row;
    padding: 0;
    align-items: flex-start;
    justify-content: space-between;
  }
}

@media (max-width: 980px) {
  .menu {
    display: none;
  }
}

/* - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ */

.menu_list {
  display: flex;
  list-style: none;
  justify-content: space-between;
}

/* - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ */

.menu_list_item {
  color: #b76882;
}

/* - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ */

.menu_list_link {
  display: flex;
  text-decoration: none;
  color: #b76882;
  text-transform: uppercase;
  font-weight: 300;
  text-align: center;
  font-size: 14px;
}

.menu_list_link:hover {
  color: #b76882;
  border-bottom: 2px solid #430307;
}

.container_hero {
  width: 100%;
  height: 80vh;
  background: url(/static/media/hero_max.e5a868cb.png) center center/cover
    no-repeat fixed padding-box rgba(67, 3, 7, 0.4);
  text-align: left;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.title_hero {
  width: 100%;
  max-width: 350px;
  font-family: 'Nunito';
  font-size: 64px;
  font-weight: bold;
  margin: 0px 30px;
}

.text_hero {
  margin: 30px;
  width: 80%;
  max-width: 600px;
  font-family: 'Open Sans';
  font-size: 24px;
}

.link_hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
}

.command_hero {
  font-family: 'Nunito';
  font-size: 14px;
  color: #ffffff;
  text-decoration: none;
  margin: 0px 0px;
}

.command_hero,
.anchor_hero,
.icon_hero {
  margin: 0px 0px;
}

:root {
  --color-background-image: #430307;
  --color-h2: #430307;
  --color-p: #8E6769;
  --size-h2: 50px;
  --size-p: 16px;
  --font-title: 'Nunito', sans-serif;
  --font-p: 'Open Sans', sans-serif;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px 0px;
  width: 100%;
}

@media (min-width: 980px) {
  .about {
    align-items: flex-end;
    flex-direction: row;
    margin: 110px 0px;
  }
}

.about__text{
  text-align: left;
  width: 100%;
}

@media (min-width: 980px) {
  .about__text {
    padding: 90px 50px 40px 100px;
    width: 40%;
  }
}

.about__text__title{
  font-family: 'Nunito', sans-serif;
  font-family: var(--font-title);
  font-size: 50px;
  font-size: var(--size-h2);
  color: #430307;
  color: var(--color-h2);
  text-align: center;
  padding: 0 20px;
}

@media (min-width: 980px) {
  .about__text__title {
    padding: 0;
    margin: 0 0 10px 0;
    text-align: left;
  }
}

.about__text__paragraph{
  font-family: 'Open Sans', sans-serif;
  font-family: var(--font-p);
  font-size: 16px;
  font-size: var(--size-p);
  color: #8E6769;
  color: var(--color-p);
  text-align: justify;
  line-height: 2;
  padding: 0 50px;
}

@media (min-width: 680px) {
  .about__text__paragraph {
    padding: 0 100px;
  }
}

@media (min-width: 980px) {
  .about__text__paragraph {
    padding: 0;
    margin: 30px 0 0 0;
  }
}

.about__image{
  background: #430307;
  background: var(--color-background-image);
  width: 100%;
  height: auto;
  padding: 70px;
  margin: 0;
  display: flex;
}

@media (min-width: 680px) {
  .about__image {
    height: 470px;
    padding: 100px;
  }
}

@media (min-width: 980px) {
  .about__image {
    padding: 70px 0 70px 100px;
    width: 60%;
  }
}

.about__image__src{
  width: 100%;
  margin: 0px;
  object-fit: contain;
}
:root {
  --color-title: #b76882;
  --color-item: #8e6769;
  --size-title: 14px;
  --size-item: 14px;
  --font-title: "Nunito", sans-serif;
  --font-item: "Open Sans", sans-serif;
}

/* Mobile */

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;
}

.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 70px;
  width: 100%;
}

.footer__logo {
  width: 97px;
  height: 50px;
}

.footer__figure__logo {
  margin: 0px;
  align-items: right;
  width: 100%;
}

.footer__list {
  text-align: center;
  padding: 0px;
  line-height: 200%;
}

.footer__list__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__list-links {
  width: 100%;
}

.footer__list__title {
  margin: 0;
  color: #b76882;
  color: var(--color-title);
  font-family: "Nunito", sans-serif;
  font-family: var(--font-title);
  font-size: 14px;
  font-size: var(--size-title);
  /* align: left; */
  vertical-align: Top;
  list-style: none;
  font-weight: bold;
  text-decoration: none;
}

.footer__list__item {
  margin: 0;
  color: #8e6769;
  color: var(--color-item);
  font-family: "Open Sans", sans-serif;
  font-family: var(--font-item);
  font-size: 14px;
  font-size: var(--size-item);
  /* align: Left; */
  vertical-align: Top;
  list-style: none;
  text-decoration: none;
}

.footer__copyright {
  display: flex;
  margin: 0;
  color: #8e6769;
  color: var(--color-item);
  justify-content: center;
  flex-wrap: nowrap;
  padding: 20px;
  border-top: 1px solid #54191d;
  font-weight: lighter;
  width: 100%;
  box-sizing: border-box;
}

.footer__list__social-network {
  display: flex;
  list-style: none;
  width: 100%;
  padding: 0px;
  justify-content: center;
}

.footer__list__item__social-network {
  padding: 0px;
}

.footer__img__social-network {
  width: 20px;
  height: 20px;
}

.footer__figure__social-network {
  margin: 3px;
}

/* Tablet */

@media (min-width: 700px) {
  .footer {
    align-items: center;
  }

  .footer__container {
    width: 100%;
  }
}

/* Desktop */

@media (min-width: 1280px) {
  .footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 274px;
    margin: 0 auto;
    list-style: none;
    overflow-x: hidden;
  }

  .footer__container {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0px;
    padding-left: 140px;
    padding-bottom: 50px;
    padding-top: 20px;
    padding-right: 80px;
    box-sizing: border-box;
  }

  .footer__img {
    width: 96.6px;
  }

  .footer__list {
    text-align: left;
  }

  .footer__list__container {
    display: flex;
    flex-direction: column;
  }

  .footer__list__logo {
    width: 30%;
  }

  .footer__figure__logo {
    display: flex;
    width: 100%;
    margin: 0px;
    align-items: center;
    padding-left: 35px;
  }

  .footer__list-links {
    width: 20%;
  }

  .footer__list__title {
    margin: 0;
    color: #b76882;
    font-weight: bold;
    font: "Nunito", sans-serif;
    font-size: 14px;
    line-height: 19px;
    line-height: 100%;
    /* Align: Left; */
    vertical-align: Top;
    text-decoration: none;
  }

  .footer__list__item {
    margin: 0;
    color: #8e6769;
    font-weight: Regular;
    font-family: "Open Sans";
    font-size: 14px;
    line-height: 21px;
    line-height: 110%;
    /* align: Left; */
    vertical-align: Top;
    list-style: none;
    line-height: 200%;
    text-decoration: none;
  }

  .linha-horizontal {
    color: #54191d;
  }

  .footer__copyright {
    display: flex;
    margin: 0;
    color: #8e6769;
    justify-content: flex-end;
    width: 100%;
    padding: 20px;
    border-top: 1px solid #54191d;
    font-weight: lighter;
    padding-right: 110px;
  }

  .footer__list__social-network {
    display: flex;
    list-style: none;
    align-items: center;
    width: 100%;
    padding: 0px;
    margin: 0px;
    justify-content: flex-start;
    margin-left: 55px;
  }

  .footer__list-contact {
    flex-wrap: nowrap;
    width: 20%;
  }
}

/*desktop*/

.ImpactedWomen {
  display: flex;
  width: 100%;
  margin: 0%;
}

.title {
  font-family: Nunito;
  font-style: normal;
  font-size: 3.125em;
  text-align: center;
  color: #430307;
}

.container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap-reverse;
  align-items: center;
  align-content: center;
  margin-bottom: 20px;
}

.image {
  margin-bottom: 115px;
  width: 525px;
  height: 346px;
  background: rgba(67, 3, 7, 0.2);
}

.content {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.name {
  width: 240px;
  font-family: Nunito;
  font-style: normal;
  font-weight: 800;
  font-size: 1.5em;
  line-height-step: 31px;

  color: #430307;
}

.content_Description {
  width: 360px;

  margin-bottom: 91px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 1em;
  line-height: 30px;

  text-align: justify;

  color: #8e6769;
  margin-left: 92px;
}

/*mobile*/

@media (min-width: 375px) {
  .image {
    width: 300px;
    height: 190px;
  }

  .title {
    margin-bottom: 63px;
  }

  .container {
    justify-content: center;
  }
}

@media (min-width: 1440px) {
  .image {
    width: 525px;
    height: 346px;
  }
}

